import { assignAdminNavbar, fetchUrl, getToken, logoutBtnOnClick, redirectTo, removeOptions } from "../util";
import { ROLES } from "../util/constants";
import { initSearch } from "../util/search";
import {
  XANO_ADMIN_SIGN_IN_REDIRECT,
  XANO_SUPER_ADMIN_ALL_TEAMS_URL,
  XANO_SUPER_ADMIN_NEW_TEAM_URL,
} from "../util/xano_urls";

let fullNameEl, emailEl, phoneNumberEl, passwordEl, gymSelectEl, roleSelectEl, addTeamBtn;
let gymSelectData = [];

$(document).ready(function () {
  gymSelectEl = document.querySelector("#team-gym-select");
  fullNameEl = document.querySelector("#team-name-input");
  emailEl = document.querySelector("#team-email-input");
  passwordEl = document.querySelector("#team-password-input");
  phoneNumberEl = document.querySelector("#team-phone-input");
  roleSelectEl = document.querySelector("#team-role-select");
  addTeamBtn = document.querySelector("#team-add-member-btn");

  gymSelectEl.disabled = true;
  fullNameEl.disabled = true;
  emailEl.disabled = true;
  passwordEl.disabled = true;
  phoneNumberEl.disabled = true;
  roleSelectEl.disabled = true;
  addTeamBtn.disabled = true;
});

window.onload = async function () {
  logoutBtnOnClick(XANO_ADMIN_SIGN_IN_REDIRECT, "sidebar-logout-btn");

 const token = getToken();
  if (token) {
    let result = await fetchUrl(XANO_SUPER_ADMIN_ALL_TEAMS_URL, "GET", false, token);

    if (result.isFetchOk !== undefined && !result.isFetchOk) {
      console.error(result.message);
      if (
        result.payload &&
        result.payload.role === ROLES.ADMIN &&
        result.payload.gyms &&
        result.payload.gyms.length > 0
      )
        redirectTo(`/admin/gym/g?gymCode=${result.payload.gyms[0].gym_id}`);
      else redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=UNAUTHORIZED_USER`);
      return;
    }

    assignAdminNavbar(ROLES.SUPER_ADMIN);
    getSuperAdminData(result, token);
  } else {
    redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
  }
};

async function getSuperAdminData(data, token) {
  gymSelectEl.disabled = false;

  appendTeams(data);
  initAddTeam(token);
  initSearch({ mode: "client", data, clientObjRefName: "name" }, appendTeams);
}

function appendTeams(data) {
  const tableContainer = document.getElementById("table-container");
  const og_webflow_element = tableContainer.querySelector("#table-item");

  function appendTeamsFn(_data) {
    let allItems = tableContainer.querySelectorAll(".adb-user.w-inline-block");
    allItems.forEach((item, indx) => {
      if (indx !== allItems.length - 1) item.remove();
    });

    _data.forEach((team, indx) => {
      const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
      clonedCard.setAttribute("id", "");
      clonedCard.setAttribute("href", `/admin/team?teamId=${team.id}`);
      let el = clonedCard.querySelectorAll(".colcontent");

      if (indx % 2 === 0) clonedCard.style.backgroundColor = "#00edf4";

      if (team.profile_picture) el[0].style.backgroundImage = `url(${team.profile_picture.url})`;

      el[1].innerText = team.name;
      el[2].innerText = team.role;
      el[4].innerText = team.email;
      el[5].innerText = team.phone_number;
      let gymCodes = "";
      team.gyms.forEach((gym, indx) => {
        gymCodes += gym.code;
        if (indx < team.gyms.length - 1) gymCodes += ", ";
        el[6].innerText = gym?.users?.length ?? 0;
      });
      el[3].innerText = gymCodes;
      el[7].innerText = team.status;

      clonedCard.style.display = "flex";
      tableContainer.prepend(clonedCard);
    });
  }

  appendTeamsFn(data);
  og_webflow_element.style.display = "none";
}

function initAddTeam(token) {
  $("form :input").attr("autocomplete", "off");
  async function fetchAddTeamSelectData() {
    let data = await fetchUrl(XANO_SUPER_ADMIN_NEW_TEAM_URL, "GET", false, token);

    if (data.isFetchOk !== undefined && !data.isFetchOk) {
      console.error(data.message);
      // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
      return;
    }

    appendTeamForm(data);

    gymSelectEl.removeEventListener("focus", fetchAddTeamSelectData);
  }
  // ? when the user clicks on the select dropdown, it will get data
  gymSelectEl.addEventListener("focus", fetchAddTeamSelectData);
}

function appendTeamForm(data) {
  removeOptions(gymSelectEl);
  data.forEach((gym) => {
    const option = document.createElement("option");
    option.setAttribute("value", gym.id);
    option.innerText = gym.code + " - " + gym.location;
    gymSelectEl.appendChild(option);
  });
  $(gymSelectEl).attr("multiple", "multiple");
  $(gymSelectEl).multiselect({
    onInitialized: function (select) {
      gymSelectData = [select[0].selectedOptions[0].value];
    },
    onChange: function () {
      var s = $(".multiselect-option.dropdown-item.active");
      var selected = [];
      $(s).each(function () {
        selected.push($(this).find(".form-check-input").val());
      });

      gymSelectData = selected;
    },
  });
  $(gymSelectEl)
    .siblings(".btn-group")
    .addClass("adb-session-dd")
    .children(".multiselect.dropdown-toggle")
    .css("height", "100%");

  removeOptions(roleSelectEl);
  Object.keys(ROLES).forEach((role) => {
    const option = document.createElement("option");
    option.setAttribute("value", role.replace(" ", "_"));
    option.innerText = role;
    roleSelectEl.appendChild(option);
  });

  fullNameEl.disabled = false;
  emailEl.disabled = false;
  passwordEl.disabled = false;
  phoneNumberEl.disabled = false;
  roleSelectEl.disabled = false;
}

var Webflow = Webflow || [];
Webflow.push(function () {
  // unbind webflow form handling (keep this if you only want to affect specific forms)
  $(document).off("submit");
  /* Any form on the page */

  $("form").on("change", function () {
    let name = fullNameEl.value;
    let email = emailEl.value;
    let password = passwordEl.value;

    if (gymSelectData.length > 0 && name !== "" && email !== "" && password !== "") {
      addTeamBtn.disabled = false;
      addTeamBtn.classList.remove("disabled");
    }
  });

  $("form").on("submit", async function (e) {
    e.preventDefault();

    const $form = $(this); // The submitted form

    let name = fullNameEl.value;
    let email = emailEl.value;
    let password = passwordEl.value;
    let phone = phoneNumberEl.value;
    let role = roleSelectEl.value;

   const token = getToken();
    if (token) {
      if (gymSelectData.length > 0 && name !== "" && email !== "" && password !== "") {
        let xano_input = {
          gyms: [...gymSelectData.map((gym) => ({ gym_id: parseInt(gym) }))],
          name,
          email,
          password,
          phone_number: phone.trim() === "" ? null : phone,
          role: role,
        };

        let result = await fetchUrl(XANO_SUPER_ADMIN_NEW_TEAM_URL, "POST", xano_input, token);

        if (result.isFetchOk !== undefined && !result.isFetchOk) {
          console.error(result.message);
          // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
          return;
        }

        window.location.reload();
      } else {
        if (gymSelectData.length === 0) $form[0][0].style.borderColor = "red";
        else $form[0][0].style.borderColor = "black";

        if (name.trim() === "") $form[0][1].style.borderColor = "red";
        else $form[0][1].style.borderColor = "black";

        if (email.trim() === "") $form[0][2].style.borderColor = "red";
        else $form[0][2].style.borderColor = "black";

        if (password.trim() === "") $form[0][3].style.borderColor = "red";
        else $form[0][2].style.borderColor = "black";

        $form
          .siblings(".w-form-done")
          .hide() // Hide success
          .siblings(".w-form-fail")
          .show() // show failure
          .text("Please fill out all (required) fields");
      }
    }
  });
});
